*{font-family:"Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;border:none;margin:0;padding:0;list-style:none;box-sizing:border-box}a{color:black;text-decoration:none}html{height:100%;background:snow}body{height:100%}.app{width:100%;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-ms-flex-line-pack:center;align-content:center}.top-bar{background-color:ghostwhite;cursor:pointer;text-shadow:0 0 10px rgba(0,0,0,0.3);font-weight:bolder;width:100px;height:30px;color:black;line-height:30px;text-align:center;opacity:0.8;box-shadow:0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);position:absolute;right:40px;top:0;-webkit-transition:opacity 0.25s;transition:opacity 0.25s}.top-bar:hover{opacity:1}.logout{top:0;right:150px}.router{min-width:990px;margin:50px auto;width:85%}



